body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}


.headerwrapper__logo--img {
    height: 2.3rem;
    padding: 10px;
    padding-top: 15px;
}

.headerwrapper__logo {
  flex-grow: 1;
}

.MuiAppBar-colorPrimary-8 {
  background-color: #fff !important;
}

.MuiButton-textPrimary-79 {
 color: #0f9dce !important;
}

ul, li {
  list-style: none;
  padding: 0;
  color: black;
}

.searchinput {
  flex-grow: 1.5;
}

.input {
  margin: 10px;
  width: 200px;
}

.invalid-input {
  border-bottom: 1px solid #f44336 !important;
  color: #f44336 !important;
}

.MuiInput-underline-116:before {
  border-bottom: 1px solid lightgray !important;
}

.MuiInput-underline-116:after {
  border-bottom: 1px solid #0f9dce !important;
}

.card--hide {
    height: 0;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
}

.card--show {
    height: 100%;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
}

.card__grid {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
}



.card__container {
    font-size: 14px;
    cursor: pointer;
    box-shadow: 0px 0px 8px grey;
    height: 250px;
    width: 200px;
    overflow: hidden;
    margin: 35px;
}

.card__container:hover {
    box-shadow: 1px 1px 8px #0f9dce;
}

.card__details {
    position: relative;
    padding: 12px;
    background-color: white;
    opacity: 0.95;
    text-align: left;
}

.card__media {
    position: relative;
}

.card__details ul {
    list-style: none;
    padding: 0;
}

.card__media img {
    width: 100%;
    height: 200px;
    border-bottom: 1px solid grey;
}

.card__media h1,h2 {
    font-size: 16px;
    vertical-align: middle;
    text-align: center;
    padding-left: 8px;
}

.edit__icon {
    position: absolute;
    z-index: 1
}

.login__form__container{
    width: 300px;
    margin: 0 auto;
    margin-top: 70px;
    text-align: center;
}

.login__form__container h1{
    color: #0f9dce;
}

.login__form{
    width: 300px;
    margin: 0 auto;
    margin-top: 60px;
}

.login__input:focus {
    outline-width: 0;
}

::-webkit-input-placeholder{
    font-size: 20px;
}

::-ms-input-placeholder{
    font-size: 20px;
}

::placeholder{
    font-size: 20px;
}

.login__input{
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    border-top: none;
    border-right: none;
    border-left: none;
    width: 300px;
    font-size: 20px;
}

.login__button{
    display: block;
    margin: 0 auto;
    margin-top: 80px;
    padding: 10px 20px 10px 20px;
    border: none;
    cursor: pointer;
    background-color: white;
    color: #0f9dce;
    font-size: 20px;
    box-shadow:0px 8px 15px rgba(0, 0, 0, 0.1);
    border-radius: 7px;
}

.login__button:focus {
    outline-width: 0;
}

.login__button:hover{
    background-color: #0f9dce;
    color: white;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px white inset;
}
.add__user__button{
    background-color:  #0f9dce;
    padding: 10px 20px 10px 20px;
    box-shadow: 0px 4px 5px lightgrey;
    font-size: 32px;
    color: white;
    margin: 40px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    position: fixed;
    bottom: 70px;
    right: 0;
}

.add__user__button:focus {
    outline:0;
}

.add__user__button:hover {
    color: white;
}

/* css for the form */
input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px white inset;
}

.add__user__form{
    display: block;
    align-items: center;
    width: 40%;
    margin: 0 auto;
    margin-top: 80px;
}

.add__user__form h1{
    color: #0f9dce;
    text-align: center;
}

.add__user__form__input{
    padding-top: 40px;
    width: 100%;
    outline: 0;
    border-top: none;
    border-left: none;
    border-right: none;
    margin: 0 auto;
    font-size: 17px;
}

.add__user__form__input::-webkit-input-placeholder{
    color: grey;
}

.add__user__form__input::-ms-input-placeholder{
    color: grey;
}

.add__user__form__input::placeholder{
    color: grey;
}

.add__user__form__button{
    display: block;
    margin: 0 auto;
    margin-top: 60px;
    padding: 10px 20px 10px 20px;
    border: none;
    cursor: pointer;
    background-color: white;
    color: #0f9dce;
    font-size: 20px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    border-radius: 7px;
}

.add__user__form__button:focus {
    outline:0;
}

.add__user__form__button:hover {
    background-color: #0f9dce;
    color: white;
}

.form__errors{
    color: red;
}

