.footer {
  height: 70px;
  background: #fff;
  display: flex;
  flex-direction: row; 
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  box-shadow: 0px 0px 8px grey;

}

.footer-icon-container {
  margin: 10px auto;
  padding: 10px;
}

.footer-icon {
  font-size: 2rem;
  margin: 5px 15px;
  color: #0f9dce;
}

.footer-icon:hover {
  color: #80d6ff;
}
